.header {
  margin-top: 35px;
  margin-bottom: 35px;
  padding-top: 20px;
  padding-bottom: 20px;

  &__name {
    font-size: 5.5rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    margin: 0 0 0 15px;
  }
}

.nav {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  margin-top: 10px;
  padding: 0;

  &__link {
    &:link,
    &:visited {
      display: inline-block;
      font-size: $default-font-size;
      font-weight: 300;
      color: $color-white;
      text-decoration: none;
      text-transform: uppercase;
      margin-left: 15px;
      border-bottom: 1px solid rgba(0, 0, 0, 0);
    }

    &:active,
    &:hover {
      color: $tertiary-color;
      border-bottom: 0.5px solid $secondary-color;
    }
  }
}
